.register_form_wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    scroll-snap-align: start;
    margin: 50px;
}

.register_container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
}

.register_form_container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    align-items: center;
    margin: 20px;
}

.register_input_field {
    background-color: #fff;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 70%;
    height: 50px;
    font-family: 'MainFont', cursive !important;
    font-size: 20px;
    color: #232323;
    border-radius: 10px;
    margin: 10px;
}

.register_validation_output{
    font-family: 'MainFont', cursive;
    font-size: 16px;
    color: red;
    letter-spacing: 0.5px;
}

.google_button {
    margin: 20px 0px;
}

@media only screen and (max-width: 1100px) {
    .register_input_field {
        background-color: #fff;
        border: none;
        padding: 12px 15px;
        margin: 8px 0;
        width: 70%;
        font-family: 'MainFont', cursive !important;
        height: 50px;
        font-size: 20px;
        color: #232323;
        border-radius: 10px;
        margin: 10px;
    }
}