.bg {
	display: flex;
	justify-content: center;
}

.box {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #060c21;
	padding: 20px;
}

.box::before {
	content: '';
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;
	background: #fff;
	z-index: -1;
}

.box::after {
	content: '';
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;
	background: #fff;
	z-index: -2;
	filter: blur(40px);
}

.box:hover {
	box-shadow: 0px 0px 10px #FE13BE, 0px 0px 15px #000052, 0px 0px 15px #8E32FF;
	transition: 0.2s ease-in;
	-webkit-transition: box-shadow 0.2s ease-in;
}

.box::before,
.box::after {
	background: linear-gradient(235deg, #FE13BE, #000052, #8E32FF);

}

